<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <el-button
          v-ripple.400
          type="primary"
          circle
          class="no-width my-0 flat"
          @click="$router.go(-1)"
        >
          <i class="bx bx-left-arrow-alt"></i>
        </el-button>
        <h5 class="my-0 ml-2">Product report</h5>
      </div>
    </div>
    <div class="col-12 list-table mt-3" v-loading="loading">
      <div class="pt-3">
        <div class="row">
          <!-- <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div> -->
          <div class="col-lg-6 col-12">
            <div class="box">
              <h3>Total Selling Amount</h3>
              <p>{{ total_selling }}mmk</p>
            </div>
          </div>

          <div class="col-lg-6 col-12">
            <div class="box">
              <h3>Total Capital Amount</h3>
              <p>{{ total_primary }}mmk</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      loading: false,
      products: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
      total_selling: "0",
      total_primary: "0",
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
  },
  mounted() {
    this.getproducts();
  },
  watch: {},
  methods: {
    getproducts() {
      this.loading = true;
      var url = `products/?page=1&page_size=1000000&query=&order_by=-updated_at`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          var data = [];
          var capital = [];
          response.data.results.forEach((product) => {
            if (product.number_of_stock !== 0) {
              data.push(
                parseInt(product.number_of_stock) * parseInt(product.price)
              );
              capital.push(
                parseInt(product.number_of_stock) *
                  parseInt(product.cost_per_item)
              );
            }
          });
          this.total_selling = data.reduce((total, item) => {
            return total + item;
          }, 0);
          this.total_primary = capital.reduce((total, item) => {
            return total + item;
          }, 0);
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

